import { createSelector } from '@ngrx/store';

import { AppStateInterface } from '@store/app/app-state.interface';
import { EditProfileStateInterface } from '@store/edit-profile/editProfileState.interface';

export const editProfileFeatureSelector = (state: AppStateInterface): EditProfileStateInterface => state.editProfile;

export const selectIsLoadingEditProfile = createSelector(editProfileFeatureSelector, (state: EditProfileStateInterface) => state.isLoading);

export const selectEmail = createSelector(editProfileFeatureSelector, (state: EditProfileStateInterface) => state.email);

export const selectIdentity = createSelector(editProfileFeatureSelector, (state: EditProfileStateInterface) => {
  return {
    firstName: state.firstName,
    lastName: state.lastName,
    birthDate: state.birthDate,
    bio: state.bio,
    website: state.website,
  };
});
